/*================= 9. RTL VERSION ==================*/
body{
	&.rtl{
		text-align: right;
		.cart-dropdown a{
			right: auto;
	    left: 15px;
		}
		.navbar-default .navbar-nav.navbar-right{
			margin: 0 0 0 50px;
		}
		.navbar-header .navbar-brand{
			padding: 18px 15px 18px 0;

		}
		.col-sm-push-6 {
			right: 50%;
			left: auto;
		}
		.about-video{
			float: left;
		}
		.about-info{
			direction: rtl;
		}
		.about-info h2 .title-about {
			padding: 0 0 0 15px;
		}
		.service-item{
			direction: rtl;
		}
		.service-adjust{
			text-align: left;
		}
		.service-icon{
			margin: 0 0 0 30px;
		}
		.service-adjust .service-icon{
			margin: 0 30px 0 0;
		}
		.list-card-item{
			direction: rtl;
		}
		.list-card-item li:last-child{
			border-right: 1px solid $border-color;
		}
		.list-card-item li:first-child{
			border: none;
		}
		.testimonial .caption{
			direction: rtl;
			padding: 0 37px 0 0;
		}
		.testimonial .caption img{
			margin: 0 0 0 20px;
		}
		.testimonial .comments p{
			text-align: right;
		}
		.list-card, .list-link, .list-address{
			direction: rtl;
		}
		.list-card li i.fa{
			margin: 0 0 0 5px;
		}
		.list-link li i.fa{
			margin: 0 0 0 10px;
		}
		.list-address li i.fa{
			text-align: right;
		}
		.copyRight p{
			text-align: center;
			@include tablet{
				text-align: right;
			}
		}
		.list-social{
			text-align: center;
			@include tablet{
				text-align: left;
			}
		}
		.page-title h2, .blog-single-title h3, .panel-sidebar .panel-heading h3{
			direction: rtl;
		}
		.page-title h2 .title-about, .blog-single-title h3 .title-about, .panel-sidebar .panel-heading h3 .title-about{
			padding: 0 0 0 15px;
		}
		.breadcrumb-custom{
			text-align: left;
		}
		.media, .media-body, form, .list-panel{
			direction: rtl;
		}
		.blog-comments .media-body{
			padding: 0 30px 0 0;
		}
		.form-groping i.fa {
			right: auto;
			left: 33px;
		}
		.list-image{
			margin: 0 0 0 20px;
		}
		.list-sidebar li a span{
			float: left !important;
		}
		aside.pull-right{
			float: left !important;
		}
		.search-field{
			direction: rtl;
		}
		.col-sm-pull-8{
			left: 0;
		}
		.dropdown-submenu a i {
			position: absolute;
			right: auto;
			top: 13px;
			left: 10px;
		}
		.dropdown-submenu > .dropdown-menu {
			left: auto;
			right: 100%;
		}
		.fa-chevron-right:before{
	    content: "\f053";
		}
		.list-sidebar li a i.fa{
			float: right;
			line-height: 21px;
			width: 20px;
			@include desktops{
				width: 33px;
			}
		}
		.mr15{
			margin-left: 15px;
		}
		.ml15{
			margin-right: 15px;
		}
		.class-list .col-sm-8, .class-grid .col-sm-8{
			float: left;
		}
		.card-list{
			direction: rtl;
		}
		.card-list-short{
			border: none;
			border-right: 1px solid $border-color;
		}
		.filter-drop .sbSelector{
			text-indent: 0;
			padding-right: 20px;
		}
		.filter-drop .sbToggle{
			right: auto;
			left: 20px;
		}
		.error-info .btn-main{
	    align-self: flex-end;
		}
		.navbar-default .navbar-toggle{
			margin-left: 80px;
			margin-right: 0;
		}
		.navbar-header .navbar-brand{
			float: right;
		}
		.copyRight .col-sm-push-6{
			right: auto;
			left: auto;
			@include tablet{
				right: 50%;
				left: auto;
			}
		}
	}
}