/*================= 6. Class ==================*/
.class-grid, .class-list{
	padding: 100px 0 0 0;
}
.card-lg-space{
	margin: 15px 0;
}

.card-list{
	background-color: $white-color;
	box-shadow: 20px 20px 15px rgba(#273143, .12);
	border-radius: 6px;
	margin-bottom: 40px;
	cursor: pointer;
	@include transition(all 0.3s ease-in-out);
	display: block;
	height: auto;
	&:hover{
		box-shadow: 6px 6px 7px rgba(#273143, .12);
	}
	@include tablet{
		display: flex;
		justify-content: space-between;
		height: 270px;
	}
}
.cart-list-img{
	display: block;
	float: none;
	width: 100%;
	border-radius: 6px 6px 0 0;
	@include tablet{
		display: inline-block;
		float: left;
		width: 35%;
		border-radius: 6px 0 0 6px;
	}
	img{
		width: 100%;
		border-radius: 6px 6px 0 0;
		height: 100%;
		@include tablet{
			border-radius: 6px 0 0 6px;
			height: auto;
		}
		@include desktops{
			height: 100%;
		}
	}
}
.card-list-details{
	padding: 15px;
	width: 100%;
	float: none;
	align-self: auto;
	@include tablet{
		padding: 0 15px;
		width: 50%;
  	float: left;
  	align-self: center;
	}
  @include desktops{
  	padding: 0 45px;
  }
	h4{
		margin: 0 0 15px 0;
		text-transform: capitalize;
		font-weight: 600;
		font-family: 'Open Sans', sans-serif;
		color: $title-color;
		letter-spacing: 0;
		font-size: 22px;
	}
	p{
		margin: 0 0 15px 0;
	}
	.btn-main{
		align-self: flex-start;
		width: 160px;
		border-radius: 25px;
	}
}
.card-list-short{
	padding: 0 10px;
	border-left: 1px solid $border-color;
	@include desktops{
		padding: 0 30px;
	}
	li{
		width: 108px;
		text-align: center;
		padding: 20% 0;
		border-bottom: 1px solid $border-color;
		&:last-child{
			border: none;
		}
		.amount{
			color: $primary-color;
			font-weight: 600;
			font-size: 18px;
		}
	}
}
.card-event{
	.card-list-details{
		width: 80%;
	}
	.cart-list-img{
		width: 100%;
		@include tablet{
			width: 40%;
		}
		img{ 
			height: 300px;
		}
	}
	.list-inline{
		margin-bottom: 20px;
		li{
			font-weight: 600;
			font-size: 13px;
			i.fa{
				color: $primary-color;
				margin-right: 7px;
			}
		}
	}
	.btn-main{
		border-radius: 0 10px;
	}
}
.class-schedule{
	margin: 76px 0 0 0;
	h3{
		font-size: 28px;
		color: $primary-color;
		display: flex;
		align-items: center;
		margin: 0 0 25px 0;
		.title-about{
			padding: 0 15px 0 0;
		}
		.shape{
			width: 56px;
			height: 11px;
			background-repeat: no-repeat;
			background-image: url(../img/shape-blue.png);
		}
	}
}
.table-schedule{
	border-color: $border-color !important;
	&.table-curved{
		thead{
			tr{
				th{
					color: $white-color;
					text-align: center;
					text-transform: uppercase;
					font-size: 14px;								
					font-weight: 600;
					padding: 0;
					height:44px;
					line-height:44px; 
					&:first-child{
						font-size: 18px;
						font-weight: 700;
						i{
							color: #c09933;
						}
					}
				}
			}
		}
		tbody{
			tr{
				td{
					padding: 0 10px;
					text-align: center;
					height:109px;
					vertical-align: middle;
					font-size: 14px;
					color: $text-color;
					border-color: $border-color !important;
					@include tablet{
						padding: 0;
					}
					&:first-child{
						font-weight: 600;
					}
					span{
						display: block;
						font-size: 18px;
						font-weight: 700;
						margin-bottom: 15px;
					}
				}
			}
		}
	}
}