/*================= 7. Blog ==================*/
.blog-section{
	padding: 85px 0 0 0;
}
.panel-sidebar{
	border: none;
	border-radius: 6px;
	margin: 0 0 50px 0;
	box-shadow: none;
	.panel-heading{
		background-color: transparent;
		border: none;
		border-radius: 6px 6px 0 0;
		padding: 0px 0 30px 0;
		h3{
			font-size: 24px;
			color: $primary-color;
			display: flex;
			align-items: center;
			margin: 0;
			.title-about{
				padding: 0 15px 0 0;
			}
			.shape{
				width: 56px;
				height: 11px;
				background-repeat: no-repeat;
				background-image: url(../img/shape-blue.png);
			}
		}
	}
	.panel-body{
		border: 1px solid $border-color;
		border-radius: 6px;
	}
	.panel-form{
		padding: 0;
		border: none;
		.form-group{
			margin: 0 0 20px 0;
		}
	}
}

.blog-single{
	img{
		border-radius: 8px;
		margin-bottom: 35px;
		width: 100%;
	}
	h2{
		font-family: 'Open Sans', sans-serif;
		letter-spacing: 0;
		font-size: 22px;
		font-weight: 600;
		color: $title-color;
		margin: 0 0 15px 0;
	}
	.list-blog-single{
		margin: 0 0 25px 0;
		li{
			i.fa{
				color: $primary-color;
				margin: 0 5px 0 0;
			}
			a{
				color: $text-color;
			}
		}
	}
	p{
		line-height: 26px;
		margin: 0 0 35px 0;
	}
}
.blog-single-title{
	h3{
		font-size: 24px;
		color: $primary-color;
		display: flex;
		align-items: center;
		margin: 0 0 25px 0;
		.title-about{
			padding: 0 15px 0 0;
		}
		.shape{
			width: 56px;
			height: 11px;
			background-repeat: no-repeat;
			background-image: url(../img/shape-blue.png);
		}
	}
}
.blog-comments{
	margin: 80px 0;
	.media-object{
		width: 75px;
		height: 75px;
		border-radius: 8px;
	}
	.media-heading{
		font-family: 'Open Sans', sans-serif;
		letter-spacing: 0;
		font-weight: 400;
		font-size: 16px;
		margin: 0 0 9px 0;
	}
	.media-body{
		padding-left: 30px;
		p{
			margin: 0 0 19px 0;
		}
		.reply-link{
			color: $primary-color;
			margin: 0 0 20px 0;
			display: block;
		}
	}
	.publish-date{
		font-size: 13px;
		i.fa{
			margin: 0 5px 20px 0;
		}
	}
}
.form-groping{
	margin-bottom: 25px;
	position: relative;
	i.fa{
		right: 33px;
	}
	.btn-main{
		width: 175px;
	}
}
.blog-form{
	.form-group{
		margin: 0 -15px;
	}
}
